import React, { Component } from 'react'
import Axios from 'axios';

export default class App extends Component {

  state = {
    name: [],
    isEdit: false,
    clickName: []
  }

  componentDidMount() {
    Axios.get(`https://jsonplaceholder.typicode.com/users`)
      .then(res => {
        const name = res.data;
        // console.log(name);
        this.setState({ name });
      })
  }

  editInput = (clickId) => {
    // console.log(user.key);
    // const arr = user.id.toString();

    // console.log(arr);
    // console.log(e.target.id);
    // // console.log(this.state);
    // const editUser = this.state.name.find((eu) => {
    //   const idEu = eu.id.toString();
    //   if (idEu === e.target.id) {
    //     return eu;
    //   }
    // });
    this.setState({ isEdit: !this.state.isEdit, clickName: clickId });
  }
 
  saveInput = () => {
    const { name } = this.state;
    // console.log("name", ...name)
    this.setState({ name, isEdit: false });
  }

  handleChange = (e) => {
    // console.log(e.target.value);
    this.setState({ name: e.target.value});
  }

  handlefocus = (event) => {
    event.target.select();
  }

  render() {
    const { name } = this.state;
    // console.log(name);
    return (
      <div>
        { name.map((user) => (
          <div key={user.id}>
          {console.log("state render", this.state.isEdit)}
            {!this.state.isEdit && this.state.clickName == {clickId} ?
              <h1>{user.name}</h1> :
              <input onFocus={this.handlefocus} autoFocus onChange={(e) => this.handleChange(e)} type="text" value={user.name} />
            }
            {!this.state.isEdit && this.state.clickName == {clickId} ?
              <button onClick={() => this.editInput(user.key)} id={user.key}>Edit</button> :
              <button onClick={() => this.saveInput()} id={user.id}>Save</button>
            }
            <br />
            <br />
          </div>
        ))}
      </div>
    )
  }
}
